import React, { useState, useEffect } from "react";

import { Button, TextField } from "@material-ui/core/";
import Todo from "./todoView/Todo";

import db from "./todoView/firebase";
import firebase from "firebase"
import Myapp from './myapp'
export default function App() {
  let [data, setData] = useState("");
  let [allData, setAllData] = useState('');

//[{id:1,noteData:"Pre Action Data"}, {id:2,noteData:"Pre Action Data2"}]

  useEffect(() => {
    console.log("Maga base");

    // db.collection("MYTODO").get().then((snapshot)=>{
    //   snapshot.docs.forEach(doc=>{
    //     ///setAllData((prev)=>{return [...prev ,doc.data().food]})
    //     console.log(doc.data())//.fruits,doc.data().food
    //   })
    // })
    //console.log(db.collection("todos").get());
    db.collection("notes2").orderBy('timestamp','desc').onSnapshot(snapshot=>{
      
     let send= snapshot.docs.map((doc)=>{

      return {id:doc.id,noteData:doc.data().Note}
        // console.log(doc.data().timestamp)
        // return doc.data().Note
        
        
      })
      setAllData(send)
     
    })

   
  }, []);
  function update() {
    if (data === "" || data === null || data === undefined) {
    } else {
      

      db.collection("notes2").add({
        Note:data,
        timestamp:firebase.firestore.FieldValue.serverTimestamp()
      })
      // setAllData((prev) => {
      //   return [...prev, data];
      // });
    }
  }
  function deleteFunction(delID){
    db.collection("notes2").doc(delID).delete()
  }

  return (
    <div>
      <div>
        <div className="container">
          <div className="h4 text-center mt-3 mb-5">
            ðŸ”¥Firebase connected todo app
          </div>
          <form
            onSubmit={(e) => {
              e.preventDefault();
            }}
          >
            <div className="inputSection d-flex justify-content-between w-75 m-auto">
              <TextField
                id="standard"
                label="ðŸ“Note"
                onChange={(e) => {
                  setData(e.target.value);
                }}
                className="w-75 mr-2"
                type="text"
                value={data}
              />
              <Button
                className="ml-3"
                disabled={!data}
                type="submit"
                onClick={(e) => {
                  e.preventDefault();
                  update();
                }}
                variant="contained"
                color="primary"
              >
                Activate
              </Button>
            </div>
          </form>
          <div className="mt-5">
            {allData?<Todo deleteFunction={deleteFunction} allData={allData} />:null}
          </div>
          <div className='mt-5'>
            <Myapp/>
          </div>
        </div>
      </div>
    </div>
  );
}
