import React,{useState,useEffect} from 'react'
import { Input } from "@material-ui/core/";
import db from "./todoView/firebase";
import PrintData from './practice/printData'
import firebase from "firebase"
function Myapp() {
    let [allData,setAllData]=useState([])
    let [small,setSmall]=useState("")
    
    function delData(delID){
       // alert(delID)
        db.collection("notes2").doc(delID).delete()
    }
    useEffect(() => {
       
        db.collection("notes2").orderBy("timestamp", "desc").onSnapshot(snapshot=>{
            //setAllData([])
           let sen= snapshot.docs.map(doc=>{
                console.log("INDIDE")
               return  {id:doc.id ,note:doc.data().Note}
    
               
            })
            setAllData(sen)

        })
       
    }, []);

    function update(e){
        if(e.key==="Enter"){

            db.collection("notes2").add({
                timestamp:firebase.firestore.FieldValue.serverTimestamp(),
                Note:small
            })
        }else{

        }
        
    }
    function updateAdata(TargetData,noteToupdate){
        db.collection("notes2").doc(TargetData.id).set({
            Note:noteToupdate
        },{merge:true})
    }
    return (
        <div>
            <h1>MYAPP</h1>
            <Input onKeyPress={(e)=>{update(e)}} onChange={(e)=>{setSmall(e.target.value)}} value={small}/>

            <PrintData updateAdata={updateAdata} delData={delData} allData={allData}/>
        </div>
    )
}

export default Myapp
