import React from "react";
import { List, ListItem, Button } from "@material-ui/core/";
import FlipMove from 'react-flip-move';
import DeleteForever from '@material-ui/icons/DeleteForever';
function Todo(props) {
  return (
    <ol>
        <FlipMove duration="3000" ease="ease-in-out">
      {props.allData.length>0?props.allData.map((value, index) => {
        return (
          <List key={index}>
            <ListItem className="h5">Note</ListItem>
            <ListItem>
              {index + 1}. {value.noteData}
            </ListItem>
            <ListItem>
              <Button
                className=""
               
                type="submit"
                onClick={(e) => {

                  e.preventDefault();
                  props.deleteFunction(value.id)

                }}
                variant="contained"
                color="secondary"
               
               
                
                startIcon={<DeleteForever />}
              >
                Delete
             
              </Button>
            </ListItem>
          </List>
        );
      }):null}</FlipMove>
    </ol>
  );
}

export default Todo;
