import React from "react";
import { List, ListItem, Button } from "@material-ui/core/";
import FlipMove from "react-flip-move";

import NewModel from "./NEWModel"

function PrintData({ allData, delData,updateAdata }) {
 
  return (
    <div>
      
      <ul className="">
        <FlipMove duration={2000} ease="easeIn">
          {allData.length > 0
            ? allData.map((val) => {
                return (
                  <List key={val.id}>
                      <NewModel change={updateAdata} id={val.id} data={val}/>
                    <ListItem className="">{val.note}</ListItem>
                    <ListItem>
                      <Button
                        variant="contained"
                        onClick={() => {
                          delData(val.id);
                        }}
                        color="secondary"
                      >
                        Delete
                      </Button>
                    </ListItem>
                  </List>
                );
              })
            : null}
        </FlipMove>
      </ul>
    </div>
  );
}

export default PrintData;
